import React, {useCallback, useEffect, useRef, useState} from 'react';
import {COMPANY_PERMISSIONS} from "../../../pages/CompaniesPages/CompanyPage";
import {useForm} from "react-hook-form";
import AppInputWrapper from "../../../components/ui/AppInputWrapper";


function CreateProductRequestForm (
    {
        onSubmit = () => {
        },
        requestErrors = {},
        name = "",
        companies = [],
    }) {

    const {
        register, handleSubmit, setError,
        setValue, formState: {errors, isValid, isDirty}
    } = useForm();

    useEffect(() => {
        setValue("product", name)
    }, [name])

    useEffect(() => {
        if (typeof requestErrors === 'object') {
            for (let key in requestErrors) {
                setError(key, {
                    type: 'custom',
                    message: requestErrors[key]
                })
            }
        }
    }, [requestErrors])

    const onLocalSubmit = useCallback((data) => {
        console.log("LOCAL SUBMIT:", data)
        onSubmit(data)
    }, [onSubmit])

    const [totalError, setTotalError] = useState("")

    useEffect(() => {
        // if (error) {
        //     setTotalError(error)
        //     return
        // }
        const hasKeys = !!Object.keys(requestErrors).length;
        if (hasKeys) {
            setTotalError("Требуется исправить некоторые поля")
        } else {
            setTotalError("")
        }
    }, [requestErrors])


    return <form onSubmit={handleSubmit(onLocalSubmit)}>
        <h4>Создание запроса поставщику</h4>
        <p>Поставщик получит анонимное уведомление о возможности выполнить заказ. В случае подтверждения заказа мы отправим вам контакты.</p>
        <AppInputWrapper
            label={"Продукт"}
            errors={errors}
            name={"product"}>
            <input
                disabled
                type={"text"}
                placeholder={"Продукт"}
                {...register("product")}/>
        </AppInputWrapper>
        <AppInputWrapper
            label={"Желаемое количество"}
            errors={errors}
            name={"amount"}>
            <input
                placeholder={"От 1 товара. 0, если не измеряется в штуках."}
                type={"text"}
                {...register("amount",
                    {
                        required: "Необходимо заполнить",
                        min: {
                            value: 1,
                            message: "Минимальное значение: 0"
                        },
                    })}/>
        </AppInputWrapper>
        <AppInputWrapper
            label={"Компания-заказчик"}
            errors={errors}
            name={"customer"}>
            <select
                {...register("customer", {required: "Необходимо выбрать компанию"})}
            >
                {companies.map(({id, name}) =>
                    <option value={id} key={id.toString()}>
                        {name}
                    </option>)}
            </select>
        </AppInputWrapper>
        <input type="submit" value={"Создать заказ"} />
    </form>
}

export default CreateProductRequestForm;
