import React, {useCallback, useEffect, useRef, useState} from 'react';
import {COMPANY_PERMISSIONS} from "../../../pages/CompaniesPages/CompanyPage";
import {useForm} from "react-hook-form";
import AppInputWrapper from "../../../components/ui/AppInputWrapper";

const dataArray = [
    {
        name: COMPANY_PERMISSIONS.COMPANY_INFO_PERMISSION,
        text: "Редактировать информацию о компании",
    },
    {
        name: COMPANY_PERMISSIONS.ADD_USERS_PERMISSION,
        text: "Добавление новых пользователей",
    },
    {
        name: COMPANY_PERMISSIONS.PRODUCT_INTERACTION_PERMISSION,
        text: "Добавление и редактирование товаров и услуг компании",
    },
    {
        name: COMPANY_PERMISSIONS.FACTORY_PERMISSION,
        text: "Полный доступ к добавлению и редактированию заводов",
    },
    {
        name: COMPANY_PERMISSIONS.ORDERING_PERMISSION,
        text: "Выполнение поиска и выбор цепи поставок",
    },
    {
        name: COMPANY_PERMISSIONS.PROPOSAL_PERMISSION,
        text: "Принимать/отклонять запросы"
    }
]

function AddCompanyUserForm(
    {
        onSubmit = () => {
        },
        loading = false,
        requestErrors = {},
        companyName = "",
    }) {

    const {
        register, handleSubmit, setError,
        setValue, formState: {errors, isValid, isDirty}
    } = useForm();

    // const [amount, setAmount] = useState(1)
    // const [productPermission, setProductPermission] = useState(false)
    // const [usersPermission, setUsersPermission] = useState(false)
    // const [infoPermission, setInfoPermission] = useState(false)
    // const [ordersPermission, setOrdersPermission] = useState(false)
    // const [factoryPermission, setFactoryPermission] = useState(false)
    // const [proposalPermission, setProposalPermission] = useState(false)

    useEffect(() => {
        setValue("company", companyName)
    }, [companyName])

    useEffect(() => {
        if (typeof requestErrors === 'object') {
            for (let key in requestErrors) {
                setError(key, {
                    type: 'custom',
                    message: requestErrors[key]
                })
            }
        }
    }, [requestErrors])

    const onLocalSubmit = useCallback((data) => {
        console.log("LOCAL SUBMIT:", data)
        onSubmit(data)
    }, [onSubmit])

    const [totalError, setTotalError] = useState("")

    useEffect(() => {
        // if (error) {
        //     setTotalError(error)
        //     return
        // }
        const hasKeys = !!Object.keys(requestErrors).length;
        if (hasKeys) {
            setTotalError("Требуется исправить некоторые поля")
        } else {
            setTotalError("")
        }
    }, [requestErrors])


    return <form onSubmit={handleSubmit(onLocalSubmit)}>
        <h2>Создание аккаунтов</h2>
        {/*<input {...register("checkbox")} type="checkbox" value="A"/>*/}
        {/*<input {...register("checkbox")} type="checkbox" value="B"/>*/}
        {/*<input {...register("checkbox")} type="checkbox" value="C"/>*/}
        <AppInputWrapper
            label={"Количество аккаунтов"}
            errors={errors}
            name={"amount"}>
            <input
                placeholder={"От 1 до 10 аккаунтов"}
                type={"text"}
                {...register("amount",
                    {
                        required: "Необходимо заполнить",
                        min: {
                            value: 1,
                            message: "Минимальное значение: 1"
                        },
                        max: {
                            value: 10,
                            message: "Максимальное значение: 10"
                        },
                    })}/>
        </AppInputWrapper>
        <AppInputWrapper
            label={"Компания"}
            errors={errors}
            name={"company"}>
            <input
                disabled
                type={"text"}
                placeholder={"Комания"}
                {...register("company")}/>
        </AppInputWrapper>
        {dataArray.map((
            {
                name,
                text,
            }) =>
            <AppInputWrapper
                key={name}
                label={text}
                type={"checkbox"}
                errors={errors}
                name={"permissions"}>
                <input
                    type={"checkbox"}
                    value={name}
                    id={name}
                    // placeholder={"Комания"}
                    {...register("permissions")}/>
            </AppInputWrapper>)}
        <input type="submit" value={"Создать"} />
    </form>
}

export default AddCompanyUserForm;
