import React, {useEffect, useLayoutEffect, useState} from 'react';
// import styles from "./styles.module.scss";
import RequestView from "../../../../components/RequestView";
import {useSelector} from "react-redux";
import RequestWrapper from "../../../../helpers/RequestWrapper";
import {useForm} from "react-hook-form";
import {COMPANY_TYPE_LIST, COMPANY_URL, MAIN_URL, ORGANIZATIONS_URL, PRODUCT_URL} from "../../../../constants";
import AppInputWrapper from "../../../../components/ui/AppInputWrapper";

const GET_COMPANY_REQUEST = "organizations/company/get/"
const ADD_PRODUCT_REQUEST = "products/product/create/"


function CompanyAddProductPage(
    {
        history,
        match,
    }) {

    const companyId = match.params.company;

    const {register, handleSubmit, setError,
        setValue, formState: {errors, isValid, isDirty}} = useForm();

    const cacheCompanies = useSelector(state => state.user.companies)
    const [needDoRequest, setNeedDoRequest] = useState(
        cacheCompanies.findIndex(i => (i.id === companyId && i.name)) < 0)
    const [loading, setLoading] = useState(true)
    const [name, setName] = useState("")
    const {REQUEST} = RequestWrapper();

    useLayoutEffect(() => {
        setNeedDoRequest(cacheCompanies.findIndex(i => (i.id === companyId && i.name)) < 0)
    }, [companyId, cacheCompanies])

    useLayoutEffect(() => {
        if (!needDoRequest) {
            try {
                setName(cacheCompanies.find(i => i.id === companyId).name)
            } catch (e) {
                //
            }
        }
    }, [needDoRequest])

    const onCompanyRequest = (res) => {
        // console.log("PRODUCTS::::", res.json)
        // setProducts(res.json.list)
        setName(res.json.info.name)
        setLoading(false)
    }

    const onSubmit = (info) => {
        REQUEST.POST({
            request: ADD_PRODUCT_REQUEST,
            body: {
                company: companyId,
                ...info,
            }
        }).then(res => {
            // console.log("|>> Res add Product:", res)
            if (res.status === 200) {
                const productId = res.json.id;
                history.push(MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES +
                    `/${companyId}` + COMPANY_URL.PRODUCTS + `/${productId}`)
            } else {
                for (let key in res.json) {
                    setError(key, {
                        type: 'custom',
                        message: res.json[key]
                    })
                }
            }
        })
    }

    return <RequestView
        needDoRequest={needDoRequest}
        request={`${GET_COMPANY_REQUEST}?id=${companyId}`}
        loading={loading}
        onRequest={onCompanyRequest}
    >
        <form onSubmit={handleSubmit(onSubmit)}>
            <h2>Добавление продукта</h2>
            <p>Дополнительные поля можно будет внести после заполнения данной формы</p>
            <AppInputWrapper
                label={"Компания"}
                errors={errors}
                name={"company"}>
                <input type={"text"} value={name}
                       disabled={true}/>
            </AppInputWrapper>
            <AppInputWrapper
                label={"Название товара"}
                errors={errors}
                name={"name"}>
                <input type={"text"} {...register("name",
                    { required: "Необходимо заполнить" })}
                />
            </AppInputWrapper>
            <AppInputWrapper
                label={"Цена"}
                errors={errors}
                name={"price"}>
                <input type={"text"} {...register("price",
                    {
                        required: "Необходимо заполнить",
                        valueAsNumber: true,
                    })}
                />
            </AppInputWrapper>
            {/*<AppInputWrapper*/}
            {/*    label={"Описание"}*/}
            {/*    errors={errors}*/}
            {/*    name={"price"}>*/}
            {/*    <input {...register("price", */}
            {/*        { required: "Необходимо заполнить" })}*/}
            {/*    />*/}
            {/*</AppInputWrapper>*/}
            <input type={"submit"} value={"Продолжить"}/>
        </form>
    </RequestView>
}

export default CompanyAddProductPage;
